import * as React from 'react'
import classNames from 'classnames'
import { Link } from 'gatsby'

export default function Header({ className, ...restProps }) {
  const headerClassName = classNames(['', className])

  const navItems = [
    {
      link: '/',
      name: 'Posts',
    },
    {
      link: '/about',
      name: 'About',
    },
  ]

  return (
    <header className={headerClassName} {...restProps}>
      <div className="container mx-auto flex items-center h-[80px]">
        <h1 className="mt-0 mr-7 text-2xl font-bold">
          <Link to="/">
            P<span className="text-sm">rogramming</span> Life
          </Link>
        </h1>
        <nav>
          <ul className="flex">
            {navItems.map(({ link, name }, index) => (
              <li key={link}>
                {!!index && <span>|</span>}
                <Link
                  className="px-3 transition-colors hover:text-orange-600"
                  activeClassName="text-orange-600"
                  to={link}
                  partiallyActive={link !== '/'}
                >
                  {name}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </header>
  )
}
